import React, { Component } from 'react';
import Select from 'react-select';

export class StopNav extends Component {
    constructor(props) {
        super(props);
        this.state = { stop: null };
        this.handleChange = this.handleChange.bind(this);
    }

    _nullStop = {id: null, attributes: {name: ""}};

    handleChange = (selectedOption) => {
        const stop = selectedOption ? selectedOption : this._nullStop;
        this.setState({ stop: stop });
        this.props.onChange(stop.id, stop.attributes.name);
    }

    render() {
        if (!this.props.stops.length) {
            return <div />;
        }
        return (
            <div className="col-md-4">
                <span class="select-label">Stop/Station</span>
                <Select options={this.props.stops} onChange={this.handleChange}
                        // only use component state if App hasn't given null stopId as prop
                        value={this.props.stopId ? this.state.stop : this._nullStop}
                        getOptionValue={s => s.id}
                        getOptionLabel={s => s.attributes.name}
                        isSearchable={true} isClearable={true} />
            </div>
        )
    }
}



